html, body {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
  font-size: 18px;
  font-weight: 400;
  position: relative;
}

* {
  box-sizing: border-box;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
  list-style: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

button {
  border: none;
  margin: 0;
  padding: 0;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

.container {
  width: 100vw;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 360px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 738px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1170px;
  }
}

@media screen and (min-width: 1400px) {
  .container {
    width: 1400px;
  }
}

.display-nan {
  display: none;
}

.is-open {
  display: block;
}

.scroll {
  overflow: hidden;
}

:root {
  --color_text-white: #fff;
  --color--dark-green: #1f4a21;
  --color--fair_green: #bad862;
  --color-text-dark-green: #d2dbd3;
}

.header {
  z-index: 0;
  background-color: #eae5da;
  background-image: url("main-bg.12e857bc.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding: 24px 0;
  position: relative;
}

.header__wrapper {
  align-items: center;
  display: flex;
}

.logo {
  fill: #fff;
  display: block;
}

.logo:hover {
  fill: var(--color--fair_green);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.1);
}

.nav {
  align-items: center;
  margin-left: 246px;
  display: flex;
}

.nav__span-button {
  width: 20px;
  height: 2px;
  cursor: pointer;
  background-color: #fff;
  display: block;
}

.nav__span-button:after {
  content: "";
  width: 20px;
  height: 2px;
  cursor: pointer;
  background-color: #fff;
  margin-top: -8px;
  display: block;
}

.nav__span-button:before {
  content: "";
  width: 20px;
  height: 2px;
  cursor: pointer;
  background-color: #fff;
  margin-top: 12px;
  display: block;
}

.nav__list {
  text-align: center;
  gap: 48px;
  display: flex;
}

.nav__item__link {
  color: var(--color_text-white);
  padding: 4px;
  display: block;
}

.nav__item__link:hover, .nav__item__link:focus {
  color: var(--color--fair_green);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.1);
}

.nav__button_link {
  color: var(--color_text-white);
  border: 1px solid #fff;
  border-radius: 61px;
  margin-left: auto;
  padding: 16px 32px;
  text-decoration: none;
}

.nav__button_link:hover, .nav__button_link:focus {
  background-color: var(--color--fair_green);
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  transform: scale(1.1);
}

.main {
  padding: 64px 0 118px;
}

.main__title {
  width: 841px;
  color: var(--color--fair_green);
  font-size: 96px;
  font-style: italic;
  font-weight: 500;
  line-height: 100%;
  display: block;
}

.main__title-span {
  color: var(--color_text-white);
  font-style: italic;
  font-weight: 600;
}

.wrapper-main {
  display: flex;
}

.container__text-right {
  padding-top: 113px;
  padding-left: 400px;
}

.text_about-right {
  width: 304px;
  color: #d2dbd3;
  margin-bottom: 32px;
  font-size: 16px;
  display: block;
}

.main__button-faire {
  background-color: var(--color--fair_green);
  color: #1f4a21;
  width: 256px;
  border-radius: 29px;
  justify-content: center;
  align-items: center;
  margin-top: 32px;
  padding: 16px 0;
  font-size: 16px;
  line-height: 140%;
  display: flex;
}

.main__button-faire:hover, .main__button-faire:focus {
  color: #fff;
  transform: scale(1.1);
}

.main__img {
  width: 1376px;
  margin-top: 80px;
}

.workers {
  padding-top: 138px;
  position: relative;
}

.workers__title {
  color: var(--color_text-white);
  width: 976px;
  margin-bottom: 32px;
  font-size: 56px;
  font-style: normal;
  font-weight: 400;
  line-height: 110%;
}

.workers__title-span {
  color: #bad862;
}

.workers__text {
  color: #d2dbd3;
  padding-left: 435px;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  display: block;
}

.workers__text:first-letter {
  padding-left: 140px;
}

.workers__link {
  color: var(--color_text-white);
  border: 1px solid var(--color_text-white);
  border-radius: 108px;
  padding: 8px 16px;
  position: absolute;
  top: 138px;
  right: 64px;
}

.workers__link:hover, .workers__link:focus {
  background-color: var(--color--fair_green);
  transform: scale(1.1);
}

.lead {
  z-index: 4;
  padding: 104px 0 430px;
}

.lead__list {
  grid-template-columns: repeat(2, 640px);
  grid-template-rows: 1, 384px;
  grid-auto-rows: 384px;
  gap: 32px;
  display: grid;
}

.lead__item {
  justify-content: space-between;
  padding: 32px 32px 32px 20px;
  display: flex;
  position: relative;
}

.lead__item-text {
  z-index: 2;
